body {
  margin: 0;
  padding: 0;
  background: #222;
  color: white;
  font-family: 'Thasadith', sans-serif;
  letter-spacing: 1px;
  text-transform: uppercase;
}

.background-image {
  position: fixed;
  z-index: -1;
  left: 0;
  top: 0;
  display: block;
  background-image: url(/catchFriend-React/static/media/bg.e9476c40.jpg);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  width: 100%;
  height: 100%;
  -webkit-filter: blur(2px);
          filter: blur(2px);

}

button {
    padding: 8px 15px;
    border: none;
    cursor: pointer;
    border-radius: 20px;
    color: white;
    background-color: #eb1919d7;
    transition: background-color 0.2s;
    box-shadow: 0px 0px 30px 1px rgba(0, 0, 0, 0.60);
}

.delButton:hover {
    background-color: #e66007;
}

.addPerson {
    position: fixed;
    top: 20px;
    left: 20px;
    width: 45px;
    height: 45px;
    background-color: #b8cc45de;
    border-radius: 50%;
}

.addbtn {
    background-color: rgb(94, 138, 163);

}
.users {
    padding: 0;
    width: 80%;
    text-align: center;
    margin: 45px auto;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

h4 {
    margin: 6px 0;
}

p {
    font-size: 12px;
}

.users div {
    margin: 0 auto;
    padding: 20px;
}

.person {
    min-width: 270px;
}

img {
    border-radius: 50%;
    -webkit-filter: grayscale(100%);
            filter: grayscale(100%);
    transition: 0.4s;
    box-shadow: 0px 0px 40px 3px rgba(0, 0, 0, 0.60);
}

.users div:hover>img {
    -webkit-filter: grayscale(20%);
            filter: grayscale(20%);
}
.search-box {
    position: fixed;
    top: 20px;
    right: 20px;
    background-color: #2f3640;
    height: 40px;
    border-radius: 40px;
    padding: 5px;
}

.search-btn {
    color: #e84118;
    float: right;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: #2f3640;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.search-txt {
    border: none;
    background: none;
    outline: none;
    float: left;
    padding: 0;
    color: white;
    font-size: 16px;
    transition: .4s;
    line-height: 40px;
    width: 0px;
    transition: 0.4s;
}

.search-box:hover>.search-txt {
    width: 240px;
    padding: 0 6px;
}

.search-txt:focus {
    width: 240px;
    padding: 0 6px;
}

.search-box:hover>.search-btn {
    background-color: white;
}
.messenger {
    position: fixed;
    bottom: 0;
    right: 60px;
    width: 300px;
    height: 330px;
    box-shadow: 0px 0px 40px 3px rgba(0, 0, 0, 0.60);
    overflow: hidden;
    border-radius: 5px;
}

.head {
    height: 30px;
    line-height: 30px;
    background-color: #434d5a;
    box-shadow: 0px 0px 40px 3px rgba(0, 0, 0, 0.60);
    display: inline-block;
    width: 100%;
}

.msg-btn {
    height: 30px;
    width: 30px;
    color: #e84118;
    display: block;
    text-align: center;
    padding-top: 5px;
}

.minimalize {
    position: absolute;
    top: 0;
    right: 30px;
}

.close {
    position: absolute;
    top: 0;
    right: 0;
}

.person {
    margin: 0;
    padding-left: 10px;
    display: inline;
}

.body {
    height: 230px;
    background-color: rgb(231, 233, 243);
    overflow-y: scroll;
    color: black;
    font-size: 12px;
}

.bottom {
    height: 70px;
    background-color: white;
    box-sizing: border-box;
}

.send {
    color: rgb(163, 162, 162);
    height: 40px;
    width: 40px;
    margin: 10px auto;
    display: block;
    position: absolute;
    bottom: 5px;
    right: 5px;
    font-size: 20px;
    line-height: 40px;
    text-align: center;
}

i {
    cursor: pointer;
}

.message {
    margin: 5px;
    width: 80%;
    height: 100%;
    overflow: hidden;
    overflow-y: scroll;
    max-height: 55px;
}
